import React from 'react';

import classes from  './Backdrop.css';

const Backdrop = (props) => (
    props.show?<div className={classes.Backdrop} onClick={()=>{props.toogleEditing();if(props.cancelUpdate){props.cancelUpdate();}}}></div>:null
);

export default Backdrop;


